.complete {
  padding-top: 5rem;
  &__img {
    position: relative;
    border-radius: 5rem;
    overflow: hidden;
    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }

  &__content {
    padding: 9rem 7rem;
  }

  &__title {
    line-height: 1.2;
    margin-bottom: 3.5rem;
  }
}
