  --content-width: 172rem;
  --container-offset: 2rem;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --cold-black: #00081a;
  --white: #fff;
  --30-black: #f5f5f7;
  --grey-1: #f3f5fa;
  --grey-2: #d2d5d9;
  --grey-3: #adafb2;
  --grey-4: #888a8c;
  --accent-blue: #3d5c99;
}

